*,
::after,
::before {
    box-sizing: border-box
}

html {
    font-size: 62.5%
}

body {
    font-size: 1.6rem;
    font-family: "Open Sans", sans-serif;
    background-color: #212635;
}

a {
    text-decoration: none;
    font: inherit;
    vertical-align: baseline;
}

.cd-header {
    height: 80px;
    width: 90%;
    margin: 0 auto;
    -webkit-transition: -webkit-transform .5s;
    -moz-transition: -moz-transform .5s;
    transition: transform .5s;
    -webkit-transform: translateZ(0);
    -moz-transform: translateZ(0);
    -ms-transform: translateZ(0);
    -o-transform: translateZ(0);
    transform: translateZ(0);
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden
}

.cd-header::after {
    clear: both;
    content: "";
    display: table
}

.cd-3d-nav a::before,
.cd-3d-nav::after,
.cd-marker::before {
    content: ''
}

.cd-header.nav-is-visible {
    -webkit-transform: translateY(80px);
    -moz-transform: translateY(80px);
    -ms-transform: translateY(80px);
    -o-transform: translateY(80px);
    transform: translateY(80px)
}

@media only screen and (min-width:768px) {
    .cd-header.nav-is-visible {
        -webkit-transform: translateY(170px);
        -moz-transform: translateY(170px);
        -ms-transform: translateY(170px);
        -o-transform: translateY(170px);
        transform: translateY(170px)
    }
}

.cd-logo {
    float: left;
    margin-top: 28px;
    font-size: 2em;
    color: #fff;
    padding: .4em 2em;
    font-weight: 100
}

.cd-3d-nav-trigger {
    -webkit-animation: pulse 1.25s cubic-bezier(.66, 0, 0, 1);
    -moz-animation: pulse 1.25s cubic-bezier(.66, 0, 0, 1);
    -ms-animation: pulse 1.25s cubic-bezier(.66, 0, 0, 1);
    animation: pulse 1.25s cubic-bezier(.66, 0, 0, 1);
    animation-iteration-count: 6;
    box-shadow: 0 0 0 0 rgba(243, 215, 213, .7);
    border-radius: 50%;
    background-color: rgba(232, 76, 61, 0);
    position: relative;
    float: right;
    height: 45px;
    width: 45px;
    margin-top: 18px;
    overflow: hidden;
    text-indent: 100%;
    white-space: nowrap;
    color: transparent
}

.cd-3d-nav-trigger span,
.cd-3d-nav-trigger span::after,
.cd-3d-nav-trigger span::before {
    position: absolute;
    width: 28px;
    height: 3px;
    background-color: #FFF;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden
}

.cd-3d-nav-trigger span {
    top: 21px;
    left: 8px;
    -webkit-transition: background .2s .5s;
    -moz-transition: background .2s .5s;
    transition: background .2s .5s
}

.cd-3d-nav-trigger span::before,
.cd-3d-nav-trigger span:after {
    content: '';
    left: 0;
    -webkit-transition: -webkit-transform .2s .5s;
    -moz-transition: -moz-transform .2s .5s;
    transition: transform .2s .5s
}

.cd-3d-nav-trigger span::before {
    bottom: 8px
}

.cd-3d-nav-trigger span::after {
    top: 8px
}

.nav-is-visible .cd-3d-nav-trigger span {
    background-color: rgba(255, 255, 255, 0)
}

.nav-is-visible .cd-3d-nav-trigger span::after,
.nav-is-visible .cd-3d-nav-trigger span::before {
    background-color: #fff
}

.nav-is-visible .cd-3d-nav-trigger span::before {
    -webkit-transform: translateY(8px) rotate(-45deg);
    -moz-transform: translateY(8px) rotate(-45deg);
    -ms-transform: translateY(8px) rotate(-45deg);
    -o-transform: translateY(8px) rotate(-45deg);
    transform: translateY(8px) rotate(-45deg)
}

.nav-is-visible .cd-3d-nav-trigger span::after {
    -webkit-transform: translateY(-8px) rotate(45deg);
    -moz-transform: translateY(-8px) rotate(45deg);
    -ms-transform: translateY(-8px) rotate(45deg);
    -o-transform: translateY(-8px) rotate(45deg);
    transform: translateY(-8px) rotate(45deg)
}

.cd-3d-nav-container {
    position: fixed;
    top: 0;
    left: 0;
    height: 80px;
    width: 100%;
    background-color: #000;
    visibility: hidden;
    -webkit-perspective: 1000px;
    -moz-perspective: 1000px;
    perspective: 1000px;
    -webkit-transform: translateY(-100%);
    -moz-transform: translateY(-100%);
    -ms-transform: translateY(-100%);
    -o-transform: translateY(-100%);
    transform: translateY(-100%);
    -webkit-transition: -webkit-transform .5s 0s, visibility 0s .5s;
    -moz-transition: -moz-transform .5s 0s, visibility 0s .5s;
    transition: transform .5s 0s, visibility 0s .5s
}

.cd-3d-nav-container.nav-is-visible {
    visibility: visible;
    -webkit-transform: translateY(0);
    -moz-transform: translateY(0);
    -ms-transform: translateY(0);
    -o-transform: translateY(0);
    transform: translateY(0);
    -webkit-transition: -webkit-transform .5s 0s, visibility .5s 0s;
    -moz-transition: -moz-transform .5s 0s, visibility .5s 0s;
    transition: transform .5s 0s, visibility .5s 0s
}

@media only screen and (min-width:768px) {
    .cd-3d-nav-container {
        height: 170px
    }
}

.cd-3d-nav {
    position: relative;
    height: 100%;
    background-color: #3e3455;
    -webkit-transform: translateZ(0);
    -moz-transform: translateZ(0);
    -ms-transform: translateZ(0);
    transform: translateZ(0);
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    -webkit-transform-origin: center bottom;
    -moz-transform-origin: center bottom;
    -ms-transform-origin: center bottom;
    -o-transform-origin: center bottom;
    transform-origin: center bottom;
    -webkit-transform: rotateX(100deg);
    -moz-transform: rotateX(100deg);
    -ms-transform: rotateX(100deg);
    -o-transform: rotateX(100deg);
    transform: rotateX(100deg);
    -webkit-transition: -webkit-transform .5s;
    -moz-transition: -moz-transform .5s;
    transition: transform .5s;
    list-style-type: none;
    margin: 0px;
    padding: 0px;
}

.cd-3d-nav::after {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: #000;
    opacity: 1;
    visibility: visible;
    -webkit-transition: opacity .5s 0s, visibility .5s 0s;
    -moz-transition: opacity .5s 0s, visibility .5s 0s;
    transition: opacity .5s 0s, visibility .5s 0s
}

.cd-3d-nav li{
    height: 100%;
    width: 25%;
    float: left
}

.cd-3d-nav li:first-of-type a::before {
    background-image: url('/assets/icon-1.svg');
    background-size: contain;
    background-position: 0
}

.cd-3d-nav li:nth-of-type(2) a::before {
    background-image: url('/assets/icon-2.svg');
    background-size: contain
}

.cd-3d-nav li:nth-of-type(3) a::before {
    background-image: url('/assets/icon-3.svg');
    background-size: contain
}

.cd-3d-nav li:nth-of-type(4) a::before {
    background-image: url('/assets/icon-4.svg');
    background-size: contain
}

.cd-3d-nav li:nth-of-type(5) a::before {
    background-image: url('/assets/icon-5.svg');
    background-size: contain
}

.cd-3d-nav a {
    position: relative;
    display: block;
    height: 100%;
    color: transparent;
    -webkit-transition: background-color .2s;
    -moz-transition: background-color .2s;
    transition: background-color .2s;
}

.cd-3d-nav a::before {
    height: 32px;
    width: 32px;
    position: absolute;
    left: 50%;
    top: 50%;
    bottom: auto;
    right: auto;
    -webkit-transform: translateX(-50%) translateY(-50%);
    -moz-transform: translateX(-50%) translateY(-50%);
    -ms-transform: translateX(-50%) translateY(-50%);
    -o-transform: translateX(-50%) translateY(-50%);
    transform: translateX(-50%) translateY(-50%);
    background-size: 32px 64px;
    background-repeat: no-repeat;
    background-position: 0 0
}

.nav-is-visible .cd-3d-nav,
.nav-is-visible .cd-marker {
    -webkit-transform: translateZ(0);
    -moz-transform: translateZ(0);
    -ms-transform: translateZ(0);
    -o-transform: translateZ(0)
}

.no-touch .cd-3d-nav a:hover {
    background-color: #2b3145
}

.cd-3d-nav li .cd-selected,
.no-touch .cd-3d-nav li .cd-selected:hover {
    background-color: #212635
}

.cd-3d-nav li .cd-selected::before {
    background-position: 0 0;
    background-color: #f5f5f5
}

.nav-is-visible .cd-3d-nav {
    transform: translateZ(0)
}

.nav-is-visible .cd-3d-nav::after {
    opacity: 0;
    visibility: hidden;
    -webkit-transition: opacity .5s 0s, visibility 0s .5s;
    -moz-transition: opacity .5s 0s, visibility 0s .5s;
    transition: opacity .5s 0s, visibility 0s .5s
}

@media only screen and (min-width:768px) {
    .cd-3d-nav a {
        padding: 7.6em 1em 0;
        color: #fff;
        /* font-size: .9rem; */
        font-weight: 600;
        text-align: center;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }
    .cd-3d-nav a::before {
        top: 4.4em;
        left: 50%;
        right: auto;
        -webkit-transform: translateX(-50%);
        -moz-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
        -o-transform: translateX(-50%);
        transform: translateX(-50%)
    }
}

.cd-marker {
    position: absolute;
    bottom: 0;
    left: 0;
    height: 3px;
    width: 25%;
    -webkit-transform-origin: center bottom;
    -moz-transform-origin: center bottom;
    -ms-transform-origin: center bottom;
    -o-transform-origin: center bottom;
    transform-origin: center bottom;
    -webkit-transform: translateZ(0) rotateX(90deg);
    -moz-transform: translateZ(0) rotateX(90deg);
    -ms-transform: translateZ(0) rotateX(90deg);
    -o-transform: translateZ(0) rotateX(90deg);
    transform: translateZ(0) rotateX(90deg);
    -webkit-transition: -webkit-transform .5s, left .5s, color .5s, background-color .5s;
    -moz-transition: -moz-transform .5s, left .5s, color .5s, background-color .5s;
    transition: transform .5s, left .5s, color .5s, background-color .5s
}

.cd-marker::before {
    position: absolute;
    bottom: 3px;
    left: 50%;
    right: auto;
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    -o-transform: translateX(-50%);
    transform: translateX(-50%);
    height: 0;
    width: 0;
    border: 10px solid transparent;
    border-bottom-color: inherit
}

.nav-is-visible .cd-marker {
    transform: translateZ(0)
}

.color-1 {
    color: #9a57bd;
    background-color: #9a57bd
}

.color-2 {
    color: #c96aa4;
    background-color: #c96aa4
}

.color-3 {
    color: #d6915e;
    background-color: #d6915e
}

.color-4 {
    color: #5397c7;
    background-color: #5397c7
}

.color-5 {
    color: #77cd91;
    background-color: #77cd91
}

h1,
main h1 {
    text-align: center;
    font-size: 2.4rem;
    font-weight: 300;
    color: #fff;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale
}

main {
    -webkit-transition: -webkit-transform .5s;
    -moz-transition: -moz-transform .5s;
    transition: transform .5s;
    -webkit-transform: translateZ(0);
    -moz-transform: translateZ(0);
    -ms-transform: translateZ(0);
    -o-transform: translateZ(0);
    transform: translateZ(0);
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}

main h1 {
    margin: 2em auto
}

main.nav-is-visible {
    -webkit-transform: translateY(80px);
    -moz-transform: translateY(80px);
    -ms-transform: translateY(80px);
    -o-transform: translateY(80px);
    transform: translateY(80px)
}

#page_id {
    margin-left: 0
}

h1 {
    margin: 1em auto 2em
}

@-webkit-keyframes pulse {
    to {
        box-shadow: 0 0 0 45px rgba(232, 76, 61, 0)
    }
}

@-moz-keyframes pulse {
    to {
        box-shadow: 0 0 0 45px rgba(232, 76, 61, 0)
    }
}

@-ms-keyframes pulse {
    to {
        box-shadow: 0 0 0 45px rgba(232, 76, 61, 0)
    }
}

@keyframes pulse {
    to {
        box-shadow: 0 0 0 45px rgba(232, 76, 61, 0)
    }
}

@media only screen and (min-width:768px) {
    main.nav-is-visible {
        -webkit-transform: translateY(170px);
        -moz-transform: translateY(170px);
        -ms-transform: translateY(170px);
        -o-transform: translateY(170px);
        transform: translateY(170px)
    }
    main h1 {
        font-size: 3.6rem;
        margin: 2em auto
    }
}
